.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 2rem;
    background-color: #080808;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30%;
   
    
  }

  .music{
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-align: center;
    padding: 20px;
  }

  .h1 {
    margin-top: 35px;
    padding: 3%;
    width: 100%;
    padding: 30px;
    text-align: center;
    color: beige;
    text-shadow: #080808;
    font-size: 45px;
    background-color: grey;
  }

  .button {
    
    padding: 0 10%;
    align-items: flex-end;
    padding: 0 10%;
    float: right;

  }